export function Chat(container) {
  this.container = container,
  this.container_class = "." + container,
  this.start = function() {
    $(this.container_class).append("start");
  },
  this.append_message_left = function(){
    $(this.container_class).append(`
      <p class="text-center">
        Epicness!
      </p>
      `);
  }
}
